<template>
	<v-form v-model="valid" @submit.prevent="submit">
		<v-container>
			<v-col cols="12">
				<v-img
					:src="require('@/assets/selectImage.png')"
					class="my-3"
					contain
					height="130"
				/>
			</v-col>
			<v-col cols="12" md="12">
				<h2 class="main-header">Login to Pic Picker</h2>
			</v-col>

			<v-col cols="12" md="12">
				<v-text-field
                    :disabled="loading"
					v-model="username"
					label="User name*"
					required
					type="username"
				></v-text-field>
			</v-col>

			<v-col cols="12" md="12">
				<v-text-field
                    :disabled="loading"
					v-model="password"
					label="Password*"
					required
					type="password"
				></v-text-field>
			</v-col>

			<v-col cols="12" md="12">
				<v-btn
					class="mr-4"
					style="width: 100%"
					type="submit"
					:disabled="invalid"
				>
					<v-progress-circular
						v-if="loading"
						indeterminate
						color="primary"
						width="2"
					/>
					<div v-else>login</div>
				</v-btn>
			</v-col>

			<v-col cols="12" md="12">
				<v-row style="padding: 20px">
					<v-spacer />
					<div>
						Don't have an account?...
						<router-link to="/login/sign-up">
							Register
						</router-link>
					</div>
				</v-row>
			</v-col>
		</v-container>
	</v-form>
</template>

<script>
import { mapActions } from "vuex";
import { getUser } from "../services/firebaseUserService";
export default {
	data() {
		return {
			valid: false,
			username: "",
			password: "",
			loading: false,
		};
	},
	computed: {
		invalid() {
			return !(this.username && this.password);
		},
	},
	methods: {
		...mapActions("common", ["setCurrentUser"]),
		async submit() {
			try {
				this.loading = true;
				let usr = await getUser(this.username);
				console.log({ usr });
				if (usr && usr.password && usr.password === this.password) {
					this.setCurrentUser({...usr,id:this.username});
					this.$router.push("/");
					this.$toast.success("Welcome "+this.username);
				} else {
					this.$toast.error("Wrong user or password");
				}
			} catch (e) {
				console.error(e);
				this.$toast.error(e.message);
			} finally {
				this.loading = false;
			}
		},
	},
};
</script>

<style></style>
